import Vue from 'vue'
import Router from 'vue-router'
// import Hello from '@/components/Hello'
import HomeIndex from '@/components/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HomeIndex',
      component: HomeIndex
    }
  ]
})



